.portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
}
/*heading*/
.portfolio span:nth-of-type(1){
    color: black;
    font-size: 2rem;
    font-weight: bold;
}
.portfolio span:nth-of-type(2){
    color: rgb(255, 173, 22);
    font-size: 2.5rem;
    font-weight: bold;
}
.portfolio .swiper{
    overflow: visible!important;
}
.portfolio-slider{
margin-top: 3rem;
width: 100%;
}
.portfolio-slider .swiper-slide{
    width: 26rem;
}
.portfolio img{
    width: 19rem;
    filter:drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
    border-radius: 19px;
}