.card{
    width: 10rem;
    height: 15.5rem;
    position:absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background:rgba(244, 235, 235, 0.384);
    border-width: 7px;
    border-style:solid;
    border-color: burlywood orange;
    border-radius: 20px;
    padding: 0px 26px 2rem 26px ;
}
.card span:nth-of-type(2){
color:gray;
font-size: 16px;
}
.card > img{
    transform: scale(0.6);
    margin-bottom: -3rem;
}
.c-button{
    background: #ffffff;
    box-shadow: 0px 19px 60px rgba(0,0,0,0,08);
    border-radius: 7px;
    border: none;
    padding: 10px;
    font-size: 16px;
    color: #5290fd;
    font-weight: 550;
}